<template>

  <div class="address">
    <van-nav-bar
      title="地址管理"
      left-text="返回"
      left-arrow
      @click-left="back"
    />
    <van-address-list
      v-model="chosenAddressId"
      :list="list"
      default-tag-text="默认"
      @add="onAdd"
      @edit="onEdit"
    />
  </div>
</template>

<script>
import '../assets/less/address.less'
  export default {
    data(){
      return{
        chosenAddressId: '1',
        list: [],

      }
    },
    created(){
      this.getAddressData();
    },
    methods: {
      back(){
        this.$router.push({name: "My"});
      },
      onAdd() {
        this.$router.push({name: 'AddressEdit'})
      },
      onEdit(item) {
        // // console.log(item)
        this.$router.push({name: 'AddressEdit',query: {item}})
      },
      // 获取地址数据
      getAddressData(){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          // 跳转到登录页
          this.$router.push({name: 'Login'});
          return;
        }
        this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
        });

        this.axios({
          method: 'GET',
          url:'/findAddress',
          params:{
            appkey: this.appkey,
            tokenString: tk,
          }
        }).then(result => {
          this.$toast.clear()
          if(result.data.code == 20000){
            result.data.result.map( v => {
              let item = {
                id: v.aid,
                name: v.name,
                tel: v.tel,
                address: v.province + v.city + v.county + v.addressDetail,
                isDefault: Boolean(v.isDefault),
              };
              this.list.push(item)

              if(v.isDefault){
                this.chosenAddressId = v.aid;
                for(let key in this.currentAddress){
                  this.currentAddress[key] = item[key];
                }
              }
            })
          }
          // console.log("findAddress result ==> ",result)
        }).catch(err =>{
          this.$toast.clear()
          // console.log("findAddress result ==> ",err)
        })

      },
    },
  }
</script>

<style lang="less" scoped>

</style>